@mixin mobile {
    @media screen and (max-width: 480px) {
        @content;
    }
}
@mixin tablet {
    @media screen and (max-width: 768px) {
        @content;
    }
}
@mixin laptop {
    @media screen and (max-width: 1200px) {
        @content;
    }
}

.formContainer{
    background-color: coral;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    

    .formWrapper{
        background-color:white;
        padding: 20px 60px;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;

        .logo{
            color: darkblue;
            font-size: 24px;
            font-weight: bold;
        }
    
        .title{
            color: darkblue;
            font-size: 10px;
        }

        form{
            display: flex;
            flex-direction: column;
            gap: 15px;
            input{
                padding: 15px;
                border: none;
                border-bottom: 1px solid coral;
                &::placeholder{
                    color: rgb(175, 175, 175);
                }
            }
            button{
                background-color: wheat;
                color: white;
                border: none;
                border-bottom: 1px solid coral;
                padding: 10px;
                font-size: 15px;
                font-weight: bold;
                cursor: pointer;
            }
            label{
                display: flex;
                align-items: center;
                gap: 10px;
                color: grey;
                font-size: 12px;
                cursor: pointer;
                img{
                    width: 32px;
                }
            }
        }
        p{
            color: grey;
            font-size: 12px;
            margin-top: 10px;
        }
    }
}

.home{
    background-color:darksalmon;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    .container{
        border: 1px solid white;
        border-radius: 10px;
        width: 65%;
        height: 80%;
        display: flex;
        overflow: hidden;
        @include tablet{
            width: 90%;
        }
        .sidebar{
            flex: 1;
            background-color: coral;
            position: relative;

            .navbar{
                display: flex;
                align-items: center;
                background-color:chocolate;
                height: 50px;
                padding: 10px;
                justify-content: space-between;
                color: blanchedalmond;
                .logo{
                    font-weight: bold;
                    @include tablet{
                        display: none;
                    }
                }
                .user{
                    display: flex;
                    gap: 10px;
                    img{
                        background-color: darksalmon;
                        height: 24px;
                        width: 24px;
                        border-radius: 50%;
                        object-fit: cover;
                    }
                    button{
                        background-color: darksalmon;
                        color:blanchedalmond;
                        font-size: 10px;
                        border: none;
                        cursor: pointer;
                        border-radius: 20%;
                        @include tablet{
                            position: absolute;
                            bottom: 10px;
                        }
                    }
                }
                
            }
            .search{
                border-bottom: 1px solid gray;
                
                .searchForm{
                    padding: 10px;
                    input{
                        background-color: coral;
                        border: none;
                        color: white;
                        outline: none;
                        &::placeholder{
                            color: lightgray;
                        }
                    }
                }
            }
            .userChat{
                padding: 10px;
                display: flex;
                align-items: center;
                gap: 10px;
                color: white;
                cursor: pointer;

                &:hover{
                    background-color: lightcoral;
                }
                img{
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    object-fit: cover;
                }
                .userChatInf{
                    span{
                        font-size: 18px;
                        font-weight: bold;
                    }
                    p{
                        font-size: 14px;
                        color:lightgray;
                    }
                }
            }
        }
        .chat{
            flex: 2;

            .chatInfo{
                height: 50px;
                background-color: coral;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 10px;
                color:lightgray
            }
            .chatIcons{
                display: flex;
                gap: 10px;
                img{
                    height: 24px;
                    cursor: pointer;
                }
            }

            .messages{
                background-color: lightsalmon;
                padding: 10px;
                height: calc(100% - 160px);
                overflow: scroll;
                .message{
                    display: flex;
                    gap: 20px;
                    margin-bottom: 20px;
                    .messageInfo{
                        display: flex;
                        flex-direction: column;
                        color: grey;
                        font-weight: 300;
                        img{
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            object-fit: cover;
                        }
                    }

                    .messageContent{
                        max-width: 80%;
                        display: flex;
                        flex-direction: column;
                        gap: 10px;
                        p{
                            background-color: white;
                            padding: 10px;
                            border-radius: 0px 10px 10px 10px;
                            max-width: max-content;
                        }
                        img{
                            width: 50%;
                        }
                    }

                    &.owner{
                        flex-direction: row-reverse;
                        .messageContent{
                            align-items: flex-end;
                        }
                        
                        p{
                            background-color:darksalmon;
                            color: white;
                            border-radius: 10px 0px 10px 10px;
                        }
                    }
                }
            }

            .input{
                height: 50px;
                background-color: white;
                padding: 10px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                input{
                    width: 100%;
                    border: none;
                    outline: none;
                    color: coral;
                    font-size: 18px;
                    &::placeholder{
                        color: lightgray;
                    }
                }
                
                .send{
                    display: flex;
                    align-items: center;
                    gap: 10px;

                    img{
                        height: 24px;
                        cursor: pointer;
                    }

                    button{
                        border:none;
                        padding: 10px 15px;
                        color: white;
                        background-color: coral;
                        cursor: pointer;
                        border-radius: 20%;
                    }
                }
            }
        }
    }
}